		right: 0px;
		width: 100%;
		height: 100vh;
		top: 10px;
		margin-top: 40px;
		background-color: white;
		display: flex;
		flex-direction: column;
		align-items: center;
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
	}
	.slider {
		z-index: 0;
	}

	.navLinks li {
		opacity: 0;
	}
	.slide:first-child {
		background: url(/static/media/product2media.98979a28.png) no-repeat center top/cover;
	}
	.slide:nth-child(2) {
		/* position: absolute; */
		background-position: right;
	}
	.slide:nth-child(3) {
		/* position: absolute; */
		background-position: bottom 10px right -100px;
	}
	.slide:nth-child(4) {
		/* position: absolute; */
		background-position: right;
	}
	.slide .titleslider {
		left: 220px;
	}
	.slide.current .titleslider {
		font-size: 26px;
		-webkit-transform: translateY(770px);
		        transform: translateY(770px);
	}
	.slide:first-child .titleslider {
		left: 220px;
		font-size: 38px;
	}

	.slide.current .subtitleslider {
		font-size: 20px;
		left: 220px;
		-webkit-transform: translateY(830px);
		        transform: translateY(830px);
		top: -620px;
	}
	.slide:first-child .subtitleslider {
		left: 220px;
		top: -540px;
	}
	.slide.current .content {
		-webkit-transform: translateX(840px);
		        transform: translateX(840px);
		bottom: 200px;
	}
	.logo {
		/* position: absolute; */
		width: 100%;
		display: flex;
		margin: 0;
		justify-content: center;
		background-color: white;
	}
	.rightComp {
		position: absolute;
		top: 15px;
		width: 100%;
		right: 0px;
		margin-top: 55px;
		margin-left: 0px;
		margin-right: 0px;
		justify-content: center;
		-webkit-transform: translateX(100%);
		        transform: translateX(100%);
		opacity: 0;
		background-color: white;
	}
	.burger {
		position: absolute;
		right: 0px;
		/* margin-right: 12px; */
		display: inline-block;
	}
	.cardsProduct {
		margin-left: 60px;
	}
	.Cgfilter,
	.filterarea {
		margin: 0px;
	}
	.filtersbtn {
		grid-row: 3/3;
		grid-column: 1/1;
	}
}
.toggle .line1 {
	-webkit-transform: rotate(-45deg) translate(-8px, 6px);
	        transform: rotate(-45deg) translate(-8px, 6px);
}
.toggle .line2 {
	opacity: 0;
}
.toggle .line3 {
	-webkit-transform: rotate(45deg) translate(-8px, -6px);
	        transform: rotate(45deg) translate(-8px, -6px);
}
.burgerActive {
	-webkit-transform: translateX(0%);
	        transform: translateX(0%);
}

.registerSc {
	margin-top: 50px;
}
@-webkit-keyframes moving {
	from {
		opacity: 0;
		-webkit-transform: translateX(50px);
		        transform: translateX(50px);
	}
	to {
		opacity: 1;
		-webkit-transform: translateX(0px);
		        transform: translateX(0px);
	}
}
@keyframes moving {
	from {
		opacity: 0;
		-webkit-transform: translateX(50px);
		        transform: translateX(50px);
	}
	to {
		opacity: 1;
		-webkit-transform: translateX(0px);
		        transform: translateX(0px);
	}
}

img {
	width: 100%;
}

.Rating {
	margin-right: 10px;
	justify-self: end;
}

.sub-total {
	margin-top: 30px;
	margin-left: 300px;
}
.process-check .btn-process {
	cursor: pointer;
	color: #fff;
	margin-left: 300px;
}
.process-check .btn-process {
	background: rgb(8, 8, 31);
}

.process-check .btn-process:hover {
	background: #f64749;
	color: rgb(13, 13, 65);
}

.headingA {
	position: relative;
	display: flex;
	font-weight: 800;
	top: 70px;
	font-size: 3rem;
	height: 400px;
	background: url("https://images.unsplash.com/photo-1534227749049-06c3ed80963c?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw=&ixlib=rb-1.2.1&auto=format&fit=crop&w=750&q=80")
		no-repeat center top/cover;
	align-items: center;
	justify-content: center;
}
.line {
	margin: 0 8px;
	background: #1a202c;
	height: 0;
	width: 8px;
	transition: all 1s ease;
}

.lineon {
	height: 60px;
}
.title {
	position: relative;
	right: 20px;
	opacity: 0;
	transition: all 0.8s ease;
}
.titleon {
	-webkit-transform: translateX(30px);
	        transform: translateX(30px);
	opacity: 1;
}
.Content1,
.Content2 {
	text-align: justify;
	display: grid;
	grid-column-gap: 60px;
	-webkit-column-gap: 60px;
	        column-gap: 60px;
	grid-row-gap: 30px;
	row-gap: 30px;
	font-weight: 500;
	margin: 190px 70px;
}
.Content1 {
	grid-template-columns: 65% 35%;
}
.Content2 {
	grid-template-columns: 35% 65%;
}
.Imageabt {
	width: 400px;
	height: 400px;
	overflow: hidden;
}
.mImage {
	transition: all 0.3s ease-in;
}
.Imageabt:hover .mImage {
	transition: all 0.3s ease-in;
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}
.imagecontainer {
	height: 400px;
	width: 400px;
}
.imagecontainer::after {
	content: "";
	display: block;
	position: relative;
	z-index: -1;
	bottom: 380px;
	right: 30px;
	width: 100%;
	height: 100%;
